import React from "react";

import styles from "./Hero.module.css";
import { getImageUrl } from "../Utils";

export const Hero = () => {
  return (
    <section className={styles.container}>
      <div className={styles.content}>
        <h1 className={styles.title}>Hi, I'm Vishnu Choundur</h1>
        <p className={styles.description}>
        a Data Scientist passionate about Machine Learning and Full-Stack Development. My academic pursuit at the University of Memphis, where I'm working towards a Master of Science in Data Science, has been a journey of growth and discovery. Reach out if you'd like to learn more!
        </p>
        <a href="mailto:choundurvishnu@gmail.com" className={styles.contactBtn}>
          Contact Me
        </a>
      </div>
      <img
        src={getImageUrl("hero/heroImage.jpg")}
        alt="Hero image of me"
        className={styles.heroImg}
      />
      <div className={styles.topBlur} />
      <div className={styles.bottomBlur} />
    </section>
  );
};

