import React from "react";

import styles from "./Contact.module.css";
import { getImageUrl } from "../Utils";

export const Contact = () => {
  return (
    <footer id="contact" className={styles.container}>
      <div className={styles.text}>
        <h2>Contact</h2>
        <p>Feel free to reach out!</p>
      </div>
      <ul className={styles.links}>
        <li className={styles.link}>
          <img src={getImageUrl("contact/gmail.png")} alt="Email icon" style={{ width: "40px", height: "40px" }} />
          <a href="mailto:choundurvishnu99@gmail.com">choundurvishnu99@gmail.com</a>
        </li>
        <li className={styles.link}>
          <img
            src={getImageUrl("contact/linkedIn.png")}
            alt="LinkedIn icon" style={{ width: "40px", height: "40px" }}
          />
          <a href="www.linkedin.com/in/vishnuchoundur">linkedin.com/vishnuchoundur</a>
        </li>
        <li className={styles.link}>
          <img src={getImageUrl("contact/github.png")} alt="Github icon" style={{ width: "40px", height: "40px" }}/>
          <a href="https://github.com/choundurvishnu">github.com/choundurvishnu</a>
        </li>
      </ul>
    </footer>
  );
};

