import React from "react";
import { Viewer } from "@react-pdf-viewer/core";
import { Worker } from "@react-pdf-viewer/core";
import "@react-pdf-viewer/core/lib/styles/index.css";
import "@react-pdf-viewer/default-layout/lib/styles/index.css";

//import { getImageUrl } from "../Utils";

export const ResumeView = () => {
  // URL or path of the PDF file
  const resumeUrl = "/assets/resume/Resume_Vishnu_Ch_1.pdf"; //{getImageUrl("hero/heroImage.jpg")}  \vishnu-portfolio\public\assets\resume

  return (
    <div style={{ height: "100vh", width: "100vw", padding: "20px" }} id="resumeview">
      <h2>My Resume</h2>
      <Worker workerUrl={`https://unpkg.com/pdfjs-dist@3.11.174/build/pdf.worker.min.js`}>
        <div style={{ height: "90%", border: "1px solid #ccc" }}>
          <Viewer fileUrl={resumeUrl} />
        </div>
      </Worker>
    </div>
  );
};

