import React from "react";

import styles from "./About.module.css";
import { getImageUrl } from "../Utils";

export const About = () => {
  return (
    <section className={styles.container} id="about">
      <h2 className={styles.title}>About</h2>
      <div className={styles.content}>
        <img
          src={getImageUrl("about/aboutImage.png")}
          alt="Me sitting with a laptop"
          className={styles.aboutImage}
        />
        <ul className={styles.aboutItems}>
          <li className={styles.aboutItem}>
            <img src={getImageUrl("about/cursorIcon.png")} alt="Cursor icon" />
            <div className={styles.aboutItemText}>
              <h3>DevOps Engineer</h3>
              <p>
              Proficient in CI/CD pipelines using Jenkins, Docker, Kubernetes, and Terraform for
              scalable application deployment and automation.
              </p>
            </div>
          </li>
          <li className={styles.aboutItem}>
            <img src={getImageUrl("about/serverIcon.png")} alt="Server icon" />
            <div className={styles.aboutItemText}>
              <h3>Data Analyst</h3>
              <p>
              Developed predictive models (e.g., applicant risk analysis, house price
                prediction) using advanced ML techniques such as Random Forest, Gradient Boosting, and Neural Networks.
              </p>
            </div>
          </li>
          <li className={styles.aboutItem}>
            <img src={getImageUrl("about/cursorIcon.png")} alt="UI icon" />
            <div className={styles.aboutItemText}>
              <h3>Full-stack development</h3>
              <p>
              Experienced in React.js, HTML5, CSS3, and JavaScript for
              building user-friendly interfaces, and Spring Boot for backend microservices development.
              </p>
            </div>
          </li>
        </ul>
      </div>
    </section>
  );
};
